import uniCard from '../../components/uni-card/uni-card.vue'
import detailData from '../../data/exchangeDetails.js'


export default {
	components: {
		uniCard
	},
	data() {
		return {
			//将data文件夹中的数据读入
			order_iden: '',
			detailList: detailData.data
		}
	},
	methods: {
		
	},
	onLoad: function() {
		var myinfo = uni.getStorageSync('viewexchange');
		this.order_iden = myinfo;
	}
}
